.body {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#f7f9fa80, #f7f9fa80), url("background.6bd17fd3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
}

.root {
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
}

.root__title {
  text-transform: uppercase;
  color: #397387;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
}

.root__nav {
  max-width: 350px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 20%;
  display: flex;
}

.root__link {
  width: 100%;
  color: #397387;
  align-items: center;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  text-decoration: none;
  display: flex;
}

.root__link:before {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #397387;
  border-radius: 50%;
  flex-shrink: 0;
  margin-right: 15px;
  display: block;
}

